<template lang="html">
  <!--    -->

  <div @touchmove.prevent>
    <div v-if="!iscoupon" class="groupon">
      <div class="makesure" v-if="isgroupon">
        <div class="topic"><img :src="course.courseimg" width="100%" height="199" /></div>
        <strong>{{course.coursename}}</strong>
        <span>{{course.coursedesc}}</span>
        <hr>
        <div class="groupon_price">
          <cite></cite>
          <em> 3</em>
        </div>
        <div class="old_price">
          <cite></cite>
          <em> 9.9</em>
        </div>
        <div class="tishi">
          5
        </div>
        <!--<div class="cashcoupon" @click="showCoupon">-->
        <!--<tt></tt>-->
        <!--<img src="../../assets/pay/jiantou.png" />-->
        <!--<b v-if="couponid == ''"></b>-->
        <!--<b v-else> {{couponAmount}}</b>-->
        <!--</div>-->
      </div>
      <div class="makesure2" v-if="!isgroupon">
        <div class="topic"><img :src="course.courseimg" width="100%" height="199" /></div>
        <strong>{{course.coursename}}</strong>
        <span>{{course.coursedesc}}</span>
        <hr>
        <div class="groupon_price">
          <cite></cite>
          <em> 9.9</em>
        </div>
        <!--<div class="cashcoupon" @click="showCoupon">-->
        <!--<tt></tt>-->
        <!--<img src="../../assets/pay/jiantou.png" />-->
        <!--<b v-if="couponid == ''"></b>-->
        <!--<b v-else> {{couponAmount}}</b>-->
        <!--</div>-->
      </div>
      <div class="pay_button" @click="groupon">
        <span>{{orderAmount}}</span>
      </div>

    </div>
    <v-coupon v-if="iscoupon" :myopenid="openid" @selectItem="myselect" @backItem="backItem"></v-coupon>
  </div>
</template>

<script>
  import Coupon from '@/components/_coupon.vue'
  import {
    payOrderInfo,
    checkConcerned
  } from '@/api/pay'
  import {
    queryGroupDetails
  } from '@/api/groupon'
  import {
    share
  } from '@/api/wx'
  import {
    queryCourseById
  } from '@/api/course'
  export default {
    components: {
      'v-coupon': Coupon,
    },
    data() {
      return {
        iscoupon: false,
        isgroupon: false,
        couponitem: {}, //
        course: {
          coursename: '',
          coursedesc: '',
          courseimg: ''
        },
        orderid: '', //
        openid: '',
        groupNo: '',
        zhijie: 0, //0-  1 -
        courseId: '', //
        orderAmount: 3,
        couponAmount: '',
        couponid: '',
        orderNo: ''
      }
    },
    methods: {
      groupon() {
        let that = this;
        let mydata = {
          orderNo: this.orderNo
        }
        console.log('123', mydata)
        payOrderInfo(mydata).then(res => {
          console.log(res)
          if (res.data.statusCode == "30") {
            alert("");
          } else if (res.data.statusCode == "12007") {
            console.log(123)
            WeixinJSBridge.invoke(
              'getBrandWCPayRequest', {
                "appId": res.data.result.appId, //
                //"timeStamp":res.data.timeStamp,         //1970
                "timeStamp": String(res.data.result.timeStamp),
                "nonceStr": res.data.result.nonceStr, //
                "package": res.data.result.package,
                "signType": "MD5", //
                "paySign": res.data.result.paySign, //,
              },
              function (res) {
                if (res.err_msg == "get_brand_wcpay_request:ok") {
                  if (that.zhijie == 1) {
                    that.$router.push({
                      path: '/share',
                      query: {
                        groupNo: that.groupNo,
                        openid: that.openid,
                        orderid: that.orderid,
                        orderNo: this.$route.query.orderNo
                      }
                    });
                  } else if (that.zhijie == 2) { //
                    that.$router.push({
                      path: '/groupon',
                      query: {
                        groupNo: that.groupNo,
                        openid: that.openid,
                        orderid: that.orderid,
                        orderNo: this.$route.query.orderNo
                      }
                    });
                  } else {
                    if (that.isgroupon) { //
                      that.$router.push({
                        path: '/groupon',
                        query: {
                          groupNo: that.groupNo,
                          openid: that.openid,
                          orderid: that.orderid,
                          orderNo: this.$route.query.orderNo
                        }
                      });
                    } else { //
                      that.$router.push({
                        path: '/moneyDetail',
                        query: {
                          openid: that.openid,
                          flag: true
                        }
                      });
                    }
                  }
                }
              });
          } else if (res.data.statusCode === "12005") {
            this.$router.push({
              path: '/afterpay',
              query: {
                openid: that.openid,
                sourceId: this.$route.query.sourceId,
                courseid: this.$route.query.courseId
              }
            })
          }
        })
      },
      showCoupon() {
        var mo = function (e) {
          e.preventDefault();
        };
        document.body.style.overflow = 'auto'; //
        document.removeEventListener("touchmove", mo, false);

        this.$router.push({
          name: 'Paycoupon',
          query: {
            openid: this.openid,
            groupNo: this.groupNo,
            courseId: this.courseId,
            orderid: this.orderid,
            zhijie: this.zhijie,
            names: this.names,
          } //openid
        })
        // this.iscoupon = true;
      },
      backItem(isclose) {
        this.iscoupon = isclose;
      },
      wxshare() {
        //wxsdk
        // wx.config('');//config
        let mydata = {
          'url': window.location.href
        };
        share(mydata).then(res => {
          console.log(res)
          if (res.data.statusCode == '200') {
            wx.config({
              debug: false, // ,apialertpclogpc
              appId: res.data.result.appId, // 
              timestamp: res.data.result.timestamp, // 
              nonceStr: res.data.result.noncestr, // 
              signature: res.data.result.signature, // js
              jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline', 'hideMenuItems'] // JS
            });
            wx.ready(function () { //ready
              // configready
              let mytitle = 'ta';
              let mydesc = '';
              let mylink =
                'http://test-yunying.coolmath.cn/beec/wx/authorize?returnUrl=http://test-yunying.coolmath.cn/beec/course'; //
              //let mylink='http://test-yunying.coolmath.cn/beec/course';//
              let myimgUrl = 'http://thyrsi.com/t6/665/1548835210x2728279033.png';
              wx.hideMenuItems({
                menuList: [
                  'menuItem:copyUrl'
                ]
              });
              wx.onMenuShareAppMessage({ //   ,configJS
                title: mytitle, // 
                desc: mydesc, // 
                link: mylink, // 
                imgUrl: myimgUrl, // 
                type: '', // ,musicvideolinklink
                dataUrl: '', // typemusicvideo
                success: function () {
                  // 
                },
                cancel: function () {
                  // 
                }
              });
              wx.onMenuShareTimeline({ //
                title: mytitle, // 
                link: mylink, // 
                imgUrl: myimgUrl, // 
                success: function () {
                  // 
                },
                cancel: function () {
                  // 
                }
              });
            });
            wx.error(function (res) { //error
              // configerror
            });
          } else {

          }
        })
      },
      getData() {
        let that = this;
        let mydata = {
          'id': this.courseId
        };
        this.$nextTick(function () {
          queryCourseById(mydata).then(res => {
            // console.log("11111111111111111111111111333333333333333333333333");
            console.log(res);
            let resData = res.data;
            if (res.data.statusCode == '200') {
              that.course.coursename = resData.result.courseName;
              that.course.coursedesc = resData.result.courseDescribe;
              that.course.courseimg = resData.result.imgUrl;
            } else {
              // console.log("11111111111111111112222222222233333333");
            }
          })
        })
      }
    },
    created() {
      //
      var mo = function (e) {
        e.preventDefault();
      };
      document.body.style.overflow = 'auto'; //
      document.removeEventListener("touchmove", mo, false);

      this.openid = this.$route.query.openid;
      this.groupNo = this.$route.query.groupNo;
      this.courseId = this.$route.query.courseId;
      this.orderid = this.$route.query.orderid;
      this.orderNo = this.$route.query.orderNo;
      console.log(this.orderNo)
      this.zhijie = this.$route.query.zhijie;
      let name = this.$route.query.names;
      this.names = name;
      if (name == 99) {
        this.isgroupon = true;
        this.orderAmount = 3;
      } else {
        this.isgroupon = false;
        this.orderAmount = 9.9;
      }
      if (this.$route.query.couponid != "" && this.$route.query.couponid != undefined) {
        this.couponid = this.$route.query.couponid;
      }
      if (this.$route.query.orderAmount != "" && this.$route.query.orderAmount != undefined) {
        this.orderAmount = this.$route.query.orderAmount;
      }
      if (this.$route.query.couponAmount != "" && this.$route.query.couponAmount != undefined) {
        this.couponAmount = this.$route.query.couponAmount;
      }

      this.getData();
      this.wxshare();

    }
  }

</script>

