<template lang="html">
  <div class="invitation_newUser">
    <div class="banner">
      <img src="../../assets/mine/invitationbanner.jpg"/>
      <p class="tip">
        <span>110</span>
      </p>
    </div>
    <!--<a href="#" class="check">-->
    <div class="beeCheck">
      <p>{{honeyTotal}}</p>
      <!--<p>{{honeyTotal}}g{{moneyTotal}}</p>-->
      <p class="look" @click="look">>></p>
    </div>
    <!--</a>-->
    <div class="success_record" v-show="succe">
      <div class="container">
        <p class="title"></p>
      </div>
      <div class="menu">
        <div class="me_order" v-for="item in inviteData">
          <p class='suc_icon'>
            <!--<img src="../../assets/mine/circle.png"/>-->
            <img :src="item.userHeadImgUrl"/>
          </p>
          <p class="orderName">{{item.nickName}}</p>
          <p class='orderArrow'>{{item.createTimeL | changeTime}}</p>
        </div>
        <!--<div class="me_order">-->
          <!--<p class='suc_icon'></p>-->
          <!--<p class="orderName"></p>-->
          <!--<p class='orderArrow'>2018.08.26</p>-->
        <!--</div>-->
      </div>
    </div>
    <div class="fail_record" v-show="fail">
      <p class="no_record"></p>
    </div>
    <div class="rule">
      <div class="ruleTop">
        <p class="ruleTitle"></p>
        <p class="circleBg"></p>
      </div>
      <div class="ruleContent">
        <div>
          <p class="tipNum">1</p>
          <p></p>
        </div>
        <div>
          <p class="tipNum">2</p>
          <p>10</p>
        </div>
        <div>
          <p class="tipNum">3</p>
          <p>100</p>
        </div>
        <div>
          <p class="tipNum">4</p>
          <p></p>
        </div>
      </div>
      <div class="invitationBtn"  @click="shareMask">
        
      </div>
      <div class="empty"></div>
    </div>
    <div class="shareMask" v-show="isShareMask" @click="maskHide()">
      <p class="shareImg">
        <img src="../../assets/mine/sharetip.png"/>
      </p>
      <p class="shareTip"></p>
    </div>
  </div>
</template>
<script>
  import { Indicator, Toast} from 'mint-ui';
  import { inviteRecord,isused} from '@/api/mine';
  import { authorize, share } from '@/api/wx'

  export default {
    name: 'invitation_newUser',
    data() {
      return {
        isShareMask: false,
        honeyTotal: '',
        moneyTotal: '',
        inviteData:[],
        openid:'',
        succe:false,
        fail:false,
        useid:'',
        refresh:''
      }
    },
    mounted(){
      this.wxshare();
      this.initData();
      this.inviteInfor();
    },
    created(){
      this.useid = this.$route.query.refresh;
      // console.log(this.useid);
      this.changeStatus();
    },
    filters:{
      changeTime(date){
        let time= date.replace(/-/g,".").substring(0,11);
        return time;
      }
    },
    methods:{
      changeStatus(){
        let params ='id='+this.useid+'&three='+'';
        Indicator.open();
        isused(params).then(res => {
          let resData = res.data;
          Indicator.close();
          if(resData.statusCode == 200){
            // console.log(resData.result);
            let params ='id='+this.useid+'&three='+'three';
            Indicator.open();
            isused(params).then(res => {
              window.location.reload();
            })
          }else {
            Indicator.close();
            Toast(resData.message);
          }
        })
      },
      initData(){
        this.honeyTotal = this.$route.query.honey;
        this.moneyTotal = this.$route.query.money;
        this.openid = this.$route.query.openid;
      },
      inviteInfor(){
        let params = {
          openId: this.openid,
        };
        Indicator.open();
        inviteRecord(params).then(res => {
          // console.log(res);
          let resData = res.data;
          Indicator.close();
          if(resData.statusCode == 200){
            if(resData.result == ''){
              this.fail = true;
              this.succe = false;
            }else {
              this.inviteData = resData.result;
              // console.log(this.inviteData);
              this.fail = false;
              this.succe = true;
            }
          }else {
            Indicator.close();
            Toast(resData.message);
          }
        })
      },
      stop(){//
        var mo=function(e){e.preventDefault();};
        document.body.style.overflow='hidden';
        document.addEventListener("touchmove",mo,false);//
      },
      move(){//
        var mo=function(e){e.preventDefault();};
        document.body.style.overflow='';//
        document.removeEventListener("touchmove",mo,false);
      },
      look(){
        this.$router.push({
          name:'mine',
          query:{
            openid:this.openid
          }
        })
      },
      maskHide(){
        this.isShareMask= false;
        this.move();
      },
      shareMask(){
        this.isShareMask = true;
        this.stop();
      },
      wxshare(){
        let me = this;
        //wxsdk
        // wx.config('');//config
        let mydata = {'url':window.location.href};
        share(mydata).then(res => {
          // console.log(res)
          if(res.data.statusCode == '200'){
            wx.config({
              debug: false, // ,apialertpclogpc
              appId:res.data.result.appId, // 
              timestamp: res.data.result.timestamp, // 
              nonceStr: res.data.result.noncestr, // 
              signature: res.data.result.signature,// js
              jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline'] // JS
            });
            wx.ready(function() { //ready
              // configready
              let mytitle='ta';
              let mydesc='';
              // let mylink ='https://wangxiaonewbucket4.oss-cn-beijing.aliyuncs.com/bee-math/course/01-%E5%B0%8F%E8%9C%9C%E8%9C%82%E7%BE%8E%E9%A3%9F%E8%AE%B0.png'
              let mylink='http://test-yunying.coolmath.cn/beec/wx/authorize?returnUrl=http://test-yunying.coolmath.cn/beec/course?inviterId='+me.openid;//
              let myimgUrl='http://thyrsi.com/t6/665/1548835210x2728279033.png';
              // let myimgUrl ='https://wangxiaonewbucket4.oss-cn-beijing.aliyuncs.com/bee-math/course/01-%E5%B0%8F%E8%9C%9C%E8%9C%82%E7%BE%8E%E9%A3%9F%E8%AE%B0.png'

              wx.onMenuShareAppMessage({ //   ,configJS
                title: mytitle, // 
                desc: mydesc, // 
                link: mylink, // 
                imgUrl: myimgUrl, // 
                type: '', // ,musicvideolinklink
                dataUrl: '', // typemusicvideo
                success: function() {
                  // 
                  // alert('');
                  me.maskHide();
                },
                fail:function(){
                  alert('');
                },
                cancel: function() {
                  // 
                }
              });
              wx.onMenuShareTimeline({ //
                title: mytitle, // 
                link: mylink, // 
                imgUrl: myimgUrl, // 
                success: function() {
                  // 
                  // alert('');
                  me.maskHide();
                },
                fail:function(){
                  alert('');
                },
                cancel: function() {
                  // 
                }
              });
            });
            wx.error(function(res){//error
              // configerror
            });
          }else {

          }
        })
      },
    },
  }
</script>
<style lang="less" scoped>
  @import "index.less";
</style>



