<template>
  <div class="money_wrap">
    <!-- <div class="GroupShop">
    </div> -->
    <header>
      <img src="../../assets/honeybee/tags/banner.png">
    </header>
    <div class="money_num">
      <div class="money_num_tit"></div>
      <div class="money_num1">
        <p><b></b>100</p>
        <ul class="money_type">
          <li @click="cashSussess">
            <div class="money_typecons">
              <div></div>
              <span>
                
                <b>0</b>
              </span>
            </div>
            <div class="icon_more">
              <img src="../../assets/money/right.png">
            </div>
          </li>
          <li @click="cash">
            <div class="money_typecons">
              <div></div>
              <span>
                
                <b>40</b>
              </span>
            </div>
            <div class="icon_more">
              <img src="../../assets/money/right.png">
            </div>
          </li>
        </ul>
      </div>
      <!-- <div class="money_num1">
        <p><b></b>0</p>
        <span class="tip"></span>
      </div> -->
    </div>
    <section class="box1">
      <div :class="[rules_show?'active':'box1_left']" @click="tab(1)">
        
      </div>
      <div :class="[commit_show?'active':'box1_right']" @click="tab(2)">
        
      </div>
    </section>
    <section class="rules" v-if="rules_show">
      <div class="rules_box1">
        <img src="../../assets/honeybee/tags/yaoqinggonglue.png" alt="">
      </div>
      <div class="rules_box2">
        <div class="rules_section">
          <dl>
            <dt>
              <img src="../../assets/honeybee/tags/1step.png" alt="">
            </dt>
            <dd>
              <h1></h1>
              <p></p>
            </dd>
          </dl>
        </div>
        <div class="rules_section">
          <dl>
            <dt>
              <img src="../../assets/honeybee/tags/2step.png" alt="">
            </dt>
            <dd>
              <h1></h1>
              <p></p>
            </dd>
          </dl>
        </div>
        <div class="rules_section">
          <dl>
            <dt>
              <img src="../../assets/honeybee/tags/3step.png" alt="">
            </dt>
            <dd>
              <h1></h1>
              <p></p>
            </dd>
          </dl>
        </div>
        <div class="rules_section">
          <dl>
            <dt>
              <img src="../../assets/honeybee/tags/4step.png" alt="">
            </dt>
            <dd>
              <h1></h1>
              <p>10</p>
            </dd>
          </dl>
        </div>
        <div class="rules_section">
          <dl>
            <dt>
              <img src="../../assets/honeybee/tags/5step.png" alt="">
            </dt>
            <dd>
              <h1></h1>
              <p>20%</p>
            </dd>
          </dl>
        </div>
      </div>
      <div class="rules_box3">
        <img src="../../assets/honeybee/tags/bottom banner.png" alt="">
      </div>
    </section>
    <section class="commit" v-if="commit_show">
      <ul class="commit_main">
        <li @click="commitMan()">
          <div class="header_img">
            <img src="http://b-ssl.duitang.com/uploads/item/201510/14/20151014001324_8R3QB.jpeg">
          </div>
          <div :class="[isSuss?'userinfo':'userinfo tab']">
            <div>
              <span class="userName"></span>
              <span>40</span>
              <span class="time">2018-12-28</span>
            </div>
            <div>
              <span class="course">:</span>
              <span class="shop_status"></span>
            </div>
          </div>
        </li>
        <li>
          <div class="header_img">
            <img src="http://b-ssl.duitang.com/uploads/item/201510/14/20151014001324_8R3QB.jpeg">
          </div>
          <div :class="[!isSuss?'userinfo':'userinfo tab']">
            <div class="fail_color">
              <span class="userName"></span>
              <span>0</span>
              <span class="time">2018-12-28</span>
            </div>
            <div class="fail_color">
              <span class="course">:</span>
              <span class="shop_status"></span>
            </div>
          </div>
        </li>
      </ul>
      <div class="rules_box3">
        <img src="../../assets/honeybee/tags/bottom banner.png" alt="">
      </div>
    </section>
    <div class="tag" @click="choseCourse">
      
    </div>
    <div class="invite" @click="choseCourse">
      <img src="../../assets/honeybee/tags/yaoqing button.png">
    </div>
    <div class="shareMask" v-if="isShareMask" @click="maskHide()">
      <div class="choseSussBox">
        <img src="../../assets/honeybee/tags/arroow.png" alt="">
        <div class="choseSuss">
          <div class="choseSussTop">
            <h2></h2>
            <p>{{courseName}}</p>
          </div>
          <div class="choseSussBottom">
            
          </div>
        </div>
      </div>
    </div>
    <div class="shareMask" v-if="isShop">
      <div class="showTip">
        <p></p>
        <button @click="shopCourse"></button>
      </div>
    </div>
    <div class="shareMask" v-if="isRecommend">
      <div class="recommend">
        <h1 class="recommend_title">
          
          <img src="../../assets/honeybee/tags/close.png" alt="" @click="recommend('close')">
        </h1>
        <div class="recommend_main">
          <div class="main_box" v-for="(item,index) in courseDetail" :key="index" @click="select(index,item)">
            <div class="label">
              <span><i></i><b>40</b></span>
              <img src="../../assets/honeybee/tags/label.png" alt="" class="courseTag">
            </div>
            <div class="course_img">
              <img :src="item.imgUrl" alt="">
            </div>
            <div class="courseName">
              <img src="../../assets/honeybee/tags/NO SL.png" alt="">
              <img src="../../assets/honeybee/tags/SL.png" alt="" v-if="isSelect">
              <p class="course_name">{{item.courseName}}</p>
            </div>
          </div>
        </div>
        <button class="recommend_button" @click="recommend">
          
        </button>
      </div>
    </div>
    <div class="shareMask" v-if="cashSuss">
      <div class="cashSuss">
        <div class="cashSussTop">
          <span>: <b>+40</b></span>
          <span>: <b>-40</b></span>
        </div>
        <div class="cashSussMain">
          <li>
            <div>
              <h3></h3>
              <p class="cashTime">2018.01.03</p>
            </div>
            <b>
              -40.0
            </b>
          </li>
        </div>
        <button class="cashSuss_button" @click="cashClose">
          
        </button>
      </div>

    </div>
    <div class="shareMask" v-if="activeImg">
      <div class="save">
        <p></p>
        <p><span>98%</span></p>
        <p><span></span></p>
      </div>
      <div class="activeImg">
        <span class="close" @click="close">
          <img class="close_icon" src="../../assets/honeybee/tags/close.png">
        </span>
        <img class="active_img" src="../../assets/honeybee/tags/big pop.png">
      </div>
    </div>
    <div class="shareMask" v-if="showCommit">
      <div class="commitMan">
        <div class="commitMater">
          <div class="header_img">
            <img src="http://b-ssl.duitang.com/uploads/item/201510/14/20151014001324_8R3QB.jpeg">
          </div>
          <div :class="[isSuss?'userinfo':'userinfo tab']">
            <div>
              <span class="userName"></span>
              <span>40</span>
              <span class="time">2018-12-28</span>
            </div>
            <div>
              <span class="course">:</span>
              <span class="shop_status"></span>
            </div>
          </div>
        </div>
        <div class="commitPeo">
          <div>
            <p><span class="commitTime">2019-01-03 18:31:56</span></p>
            <p></p>
          </div>
          <div>
            +40
          </div>
        </div>
        <h2></h2>
        <div class="commitTwo">
          <div class="commitPeo">
            <div>
              <p><span class="commitTime">2019-01-03 18:31:56</span></p>
              <p></p>
            </div>
            <div>
              +40
            </div>
          </div>
        </div>
        <button class="commitButton" @click="closeCommit">
          
        </button>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    share
  } from "@/api/wx";
  import {
    MessageBox
  } from 'mint-ui';
  import {
    queryAllCourseByPayStatus
  } from '@/api/money'
  // import ElasticFrame from '@/components/_ElasticFrame';
  export default {
    data() {
      return {
        rules_show: true, // 
        commit_show: false, // 
        isShareMask: false, // 
        isShop: false, // 
        isRecommend: false, // 
        cashSuss: false, // 
        activeImg: false, // 
        showCommit: false, // 
        isSuss: true, // 
        isSelect: false,
        isSelect2: false,
        courseName: '',
        openid: '',
        course: '', // id
        courseDetail: ''
      };
    },
    created() {
      this.wxshare();
      if (this.$route.query.share === 1) {
        this.openid = this.$route.query.openid //  openid
      } else {
        this.openid = this.$route.query.openid //  
      }
      if (window.location.href === "http://test-yunying.coolmath.cn/beec/money") {
        location.href =
          'http://test-yunying.coolmath.cn/beec/wx/authorize?returnUrl=http://test-yunying.coolmath.cn/beec/money'
      }
      // if (!localStorage.getItem('open')) {
      //   location.href =
      //     'http://test-yunying.coolmath.cn/beec/wx/authorize?returnUrl=http://test-yunying.coolmath.cn/beec/money';
      //   window.localStorage.setItem('open', this.openid)
      //   this.openid = localStorage.getItem('open')
      // }
      // if (this.openid !== localStorage.getItem('open')) {
      //   location.href =
      //     'http://test-yunying.coolmath.cn/beec/wx/authorize?returnUrl=http://test-yunying.coolmath.cn/beec/money';
      //   // this.openid = localStorage.getItem('open')
      //   // window.localStorage.setItem('open', this.openid)
      //   this.openid = localStorage.getItem('open')
      // }
      queryAllCourseByPayStatus({
        pageNum: '',
        pageSize: '',
        openId: this.openid
      }).then(res => {
        console.log('123',res)
        if (res.data.result.length === 0) {
          this.isShop = true
        } else {
          this.courseDetail = res.data.result
        }
      })
    },
    mounted() {
      // 
    },
    methods: {
      closeCommit() {
        this.showCommit = false
      },
      commitMan() {
        this.showCommit = true
      },
      select(index, item) {
        if (index === 0) {
          this.isSelect = true
          this.isSelect2 = false
          this.courseName = item.courseName
          this.course = item.id
          // this.course = item.id
        } else if (index === 1) {
          this.isSelect2 = true
          this.isSelect = false
          this.courseName = item.courseName
          this.course = item.id
          // this.course = item.id
        }
      },
      close() { //  
        if (this.activeImg === true) {
          this.activeImg = false
          this.courseName = ''
        }
      },
      poster() { //  
        this.activeImg = true
      },
      cashSussess() { // 
        this.cashSuss = true
      },
      cashClose() { // 
        this.cashSuss = false
      },
      recommend(item) { // 
        if (item === 'close') {
          this.isRecommend = false;
          this.courseName = ''
        } else {
          if (this.courseName === '') {
            MessageBox.alert('', {
              message: ''
            })
          } else {
            this.isRecommend = false;
            this.isShareMask = true;
          }
        }
      },
      cash() { // 
        MessageBox.confirm('', {
          message: '',
          title: '',
          confirmButtonText: "",
          cancelButtonText: ""
        }).then(action => {}).catch(res => {
          MessageBox.alert('', {
            message: '4001-2'
          })
        });
      },
      shopCourse() { //  
        this.$router.push({
          path: '/course',
          query: {
            openid: this.openid
          }
        })
      },
      maskHide() { //   
        this.isShareMask = false;
        this.poster()
        this.move();
      },
      shareMask() { //   
        this.stop();
      },
      choseCourse() { 
        this.isRecommend = true;
        this.isSelect = false;
        this.isSelect2 = false
      },
      stop() { //
        var mo = function (e) {
          e.preventDefault();
        };
        document.body.style.overflow = 'hidden';
        document.addEventListener("touchmove", mo, false); //
      },
      move() { //
        var mo = function (e) {
          e.preventDefault();
        };
        document.body.style.overflow = ''; //
        document.removeEventListener("touchmove", mo, false);
      },
      tab(num) { // tab
        if (num === 1) {
          this.rules_show = true;
          this.commit_show = false;
        } else if (num === 2) {
          this.rules_show = false;
          this.commit_show = true;
        }
      },
      wxshare() { // 
        let _this = this;
        //wxsdk
        // wx.config('');//config
        let mydata = {
          'url': window.location.href
        };
        console.log(mydata)
        share(mydata).then(res => {
          console.log(res)
          if (res.data.statusCode == '200') {
            wx.config({
              debug: false, // ,apialertpclogpc
              appId: res.data.result.appId, // 
              timestamp: res.data.result.timestamp, // 
              nonceStr: res.data.result.noncestr, // 
              signature: res.data.result.signature, // js
              jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline'] // JS,
            });
            wx.ready(function () { //ready
              // configready
              let mytitle = '';
              let mydesc = '';
              let mylink =
                'http://test-yunying.coolmath.cn/beec/wx/authorize?returnUrl=http://test-yunying.coolmath.cn/beec/money?share=1%26inviterid=' +
                _this.openid + '%26course=' + _this.course; //
              //let mylink='http://test-yunying.coolmath.cn/beec/course';//
              let myimgUrl = 'http://thyrsi.com/t6/665/1548835210x2728279033.png';
              // wx.hideMenuItems({  //
              //   menuList: [
              //     'menuItem:copyUrl'
              //   ]
              // });
              wx.onMenuShareAppMessage({ //   ,configJS
                title: mytitle, // 
                desc: mydesc, // 
                link: mylink, // 
                imgUrl: myimgUrl, // 
                type: '', // ,musicvideolinklink
                dataUrl: '', // typemusicvideo
                success: function () {
                  // 
                },
                cancel: function () {
                  // 
                }
              });
              wx.onMenuShareTimeline({ //
                title: mytitle, // 
                link: mylink, // 
                imgUrl: myimgUrl, // 
                success: function () {
                  // 
                },
                cancel: function () {
                  // 
                }
              });
            });
            wx.error(function (res) { //error
              // configerror
            });
          }
        })
      },
    }
  };

</script>

<style lang="less" scoped>
  @import 'money.less';

</style>



