<template lang="html">
  <!--  -->
  <div class="groupon_haoyou1">
    <!-- -->
    <!--   -->
    <div class="tishi" v-if="groupState == 1">
      <span></span>
    </div>
    <div class="tishi" v-else>
      <span v-if="faqiUser.openId == openid"></span>
      <span v-else-if="currentUserPay"></span>
      <div v-else-if="!currentUserPay">
        <img :src="faqiUser.userHeadImgUrl" />
        <span >{{faqiUser.nickName}}</span>
      </div>
    </div>

    <!-- -->
    <div class="share_topic">
      <div class="topic"><img :src="mycourse.imgUrl" width="100%" height="199"/></div>
      <div class="desc"><span >{{mycourse.groupDescribe}}   </span></div>
    </div>

    <!-- -->
    <div class="count_down" v-if="groupState == 0">
      <div class="remaining_time"></div>
      <div class="time">
        <strong>{{time.hh.h1}}</strong>
        <em>{{time.hh.h2}}</em>
        <cite></cite>

        <strong>{{time.mm.m1}}</strong>
        <em>{{time.mm.m2}}</em>
        <cite></cite>

        <strong>{{time.ss.s1}}</strong>
        <em>{{time.ss.s2}}</em>
        <cite></cite>
      </div>
      <div class="road">
        5
      </div>
    </div>

    <div class="count_down" v-if="groupState == 1">
      <div class="road">
        5
      </div>
    </div>

    <!-- -->
    <div class="invitation_friends" v-if="groupState == 0 ">
      <p><em>{{remainNumber}}</em></p>
      <div class="myimages">
        <img v-for="item in users" :src="item.userHeadImgUrl" class="user_img" />
        <img v-if="users.length < 2" class="add_user" src="../../assets/groupon/adduser.png" @click="shareMask"/>
        <img v-if="users.length < 3" class="add_user" src="../../assets/groupon/adduser.png" @click="shareMask"/>
      </div>
    </div>
    <div class="invitation_friends" v-else>
      <p></p>
      <div class="myimages">
        <img v-for="item in users" :src="item.userHeadImgUrl" class="user_img" />
        <img v-if="users.length < 2" class="add_user" src="../../assets/groupon/default.png"/>
        <img v-if="users.length < 3" class="add_user" src="../../assets/groupon/default.png"/>
      </div>
    </div>

    <!--   -->
    <div  v-if="groupState == 1">
      <div class="groupon_pay" v-if="!currentUserPay">
        <div class="pay_box1">
          <p></p>
          <span>66</span>
          <em>199</em>
        </div>
        <div class="pay_box2" @click="haopay2">
          <strong></strong>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="share_weixin_friends" v-else-if="currentUserPay">
      <p><em>3</em>98%</p>
      <div class="fenhaopengyou">
        <div class="wx_fr_box">
          <img src="../../assets/groupon/weixin.png" @click="shareMask"/>
          <span></span>
        </div>
        <div class="wx_fr_box">
          <img src="../../assets/groupon/firend.png" @click="shareMask"/>
          <span></span>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="groupon_pay" v-else-if="!currentUserPay">
      <div class="pay_box1">
        <p></p>
        <span>66</span>
        <em>199</em>
      </div>
      <div class="pay_box2" @click="haopay">
        <strong></strong>
      </div>
    </div>

    <!--  --->
    <div class="view_order" v-if="currentUserPay" @click="viewOrder">
      <span></span>
    </div>

    <div class="shareMask" v-show="isShareMask" @click="maskHide">
      <p class="shareImg">
        <img src="../../assets/mine/sharetip.png"/>
      </p>
      <p class="shareTip"></p>
    </div>

  </div>
</template>
<script>
  import Time from '@/utils/time';
  import { queryGroupDetails } from '@/api/groupon'
  import { share ,reload} from '@/api/wx'
  import { createOrderInfo } from '@/api/pay'
  import { isused} from '@/api/mine';

  export default {
    data(){
      return {
        nickname:"",//
        remain:'',
        time:{
          hh:{
            h1:'0',
            h2:'0'
          },
          mm:{
            m1:'0',
            m2:'0'
          },
          ss:{
            s1:'0',
            s2:'0'
          }
        },
        groupNo:'',
        users:[],//
        faqiUser:{},//,
        mycourse:{},//
        groupState:'',//01
        remainNumber:2,//
        openid:'',//openid,
        currentUserPay:false,//
        isShareMask: false,
        orderid:'',//id
        isCurrentUsers:false,//
        useid:'',//
      }
    },
    methods:{
      countTime(){
        if(this.remain !=''){
          this.remain = this.remain-1;
          if(this.remain>0){
            this.time = Time.formattime(this.remain)
            setTimeout(this.countTime,1000);
          }
        }
      },
      initData(){
        let that = this;
        let mydata = {'groupNo':this.groupNo} ;
        //let mydata = {'groupNo':'PTf7480b2f57c94b6cb4386bf207aac32e'} ;
        queryGroupDetails(mydata).then(res => {
          console.log(res)
          if(res.data.statusCode == '200'){

            that.mycourse.courseId =  res.data.result.courseId;
            that.mycourse.courseName =  res.data.result.courseName;
            that.mycourse.courseDescribe =  res.data.result.courseDescribe;
            that.mycourse.groupDescribe =  res.data.result.groupDescribe;
            that.mycourse.coursePrice =  res.data.result.coursePrice;
            that.mycourse.imgUrl =  res.data.result.imgUrl;
            that.groupState =  res.data.result.groupState;
            // that.groupState =  1;

            let users_db  = res.data.result.userList;
            that.users =  users_db;
            users_db.forEach(item=>{
              if(item.isInit == 0){
                this.faqiUser = item;
              }
            })
            that.remainNumber = 3-users_db.length;

            //
            let ojb = users_db.filter(item2=>item2.openId == this.openid)[0];//
            if(ojb != null && ojb != undefined){//
              this.currentUserPay = true;
            }

            console.log(res.data.result.groupTime)
            that.remain= Time.initTime(res.data.result.groupTime,5);
            if(that.remain !='') {
              that.time = Time.formattime(that.remain)
            }
            that.countTime();

            // 
            if(res.data.result.groupState==1 && that.faqiUser.openId==that.openid){
              this.$router.push({
                path:'/course',
                query: {
                  openid: that.openid,
                  flag: false,//
                }
              });
            }

            this.wxshare();

          }else {

          }
        })
      },
      haopay(){//
        let that = this;
        let mydata = {
          'openId':that.openid,
          'courseId': that.mycourse.courseId,
          'orderAmount': 66,
          'orderSource': 1,
          'groupNo': that.groupNo,
          'sourceId':that.faqiUser.openId
        };
        createOrderInfo(mydata).then(res => {
          if(res.data.statusCode == '200'){
            that.$router.push({
              path:'/pay',
              query:{
                openid:this.openid,
                groupNo: that.groupNo,
                courseId: that.mycourse.courseId,
                zhijie:1,//
                "names":99,
                "courseid":res.data.result.courseId,
                "orderid":res.data.result.id,
              }
            });
          }else if(res.data.statusCode == '27'){//
            window.location.href = window.location.href;
            window.location.reload()
          }else if(res.data.statusCode == '29'){
            alert("");
          }
        })
      },
      haopay2(){//
        let that = this;
        let mydata = {
          'openId':this.openid,
          'courseId': that.mycourse.courseId,
          'orderAmount': 66,
          'orderSource':0,//
          'sourceId':that.faqiUser.openId
        };
        createOrderInfo(mydata).then(res => {
          if(res.data.statusCode == '200'){
            that.$router.push({
              path:'/pay',
              query:{
                openid:that.openid,
                groupNo: res.data.result.groupNo,
                courseId: that.mycourse.courseId,
                zhijie:2,//
                "names":99,
                "courseid":res.data.result.courseId,
                "orderid":res.data.result.id,
              }
            });
          }else if(res.data.statusCode == '29'){
            alert("");
          }
        })
      },
      wxshare(){
        let that = this;
        //wxsdk
        // wx.config('');//config
        let mydata = {'url':window.location.href};
        share(mydata).then(res => {
          console.log(res)
          if(res.data.statusCode == '200'){
            wx.config({
              debug: false, // ,apialertpclogpc
              appId:res.data.result.appId, // 
              timestamp: res.data.result.timestamp, // 
              nonceStr: res.data.result.noncestr, // 
              signature: res.data.result.signature,// js
              jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline'] // JS
            });
            wx.ready(function() { //ready
              // configready
              let mytitle= that.mycourse.courseName;
              let mydesc='';
              let mylink='http://test-yunying.coolmath.cn/beec/wx/authorize?returnUrl=http://test-yunying.coolmath.cn/beec/share?groupNo='+that.groupNo;// id
              let myimgUrl= that.mycourse.imgUrl;

              wx.onMenuShareAppMessage({ //   ,configJS
                title: mytitle, // 
                desc: mydesc, // 
                link: mylink, // 
                imgUrl: myimgUrl, // 
                type: '', // ,musicvideolinklink
                dataUrl: '', // typemusicvideo
                success: function() {
                  // 
                  that.maskHide();
                },
                cancel: function() {
                  // 
                }
              });
              wx.onMenuShareTimeline({ //
                title: mytitle, // 
                link: mylink, // 
                imgUrl: myimgUrl, // 
                success: function() {
                  // 
                  that.maskHide();
                },
                cancel: function() {
                  // 
                }
              });
            });
            wx.error(function(res){//error
              // configerror
            });
          }else {

          }
        })
      },
      maskHide(){
        this.isShareMask= false;
      },
      shareMask(){
        this.isShareMask = true;
        // this.stop();
      },
      isLoad(){
        let that = this;
        let mydata = {'orderId':that.orderid} ;
        //let mydata = {'groupNo':'PTf7480b2f57c94b6cb4386bf207aac32e'} ;
        if(that.orderid!=undefined){
          reload(mydata).then(res => {
            if (res.data.result == 'true') {
              console.log('111111111111111111111111'+res.data.result)
              let mydata2 = {'orderId':that.orderid} ;
              reload(mydata2).then(res => {
                if (res.data.result == 'false') {
                  window.location.href = window.location.href;
                  window.location.reload()
                }
              });
            }
          });
        }
        // 
        if(that.useid!=undefined){
          let params ='id='+that.useid+'&three='+'';
          isused(params).then(res => {
            let resData = res.data;
            if(resData.statusCode == 200){
              let params ='id='+this.useid+'&three='+'three';
              isused(params).then(res => {
                window.location.reload();
              })
            }
          })
        }

      },
      viewOrder(){
        window.location.href="http://test-yunying.coolmath.cn/beec/wx/authorize?returnUrl=http://test-yunying.coolmath.cn/beec/course";
      },
    },
    created(){
      this.openid = this.$route.query.openid;
      this.groupNo = this.$route.query.groupNo;
      this.orderid = this.$route.query.orderid;
      this.useid = this.$route.query.useid;

      this.isLoad();
      this.initData();

      //
      // this.remain= Time.initTime(1536733716692,5);
      // if(this.remain !='') {
      //   this.time = Time.formattime(this.remain)
      // }
      //
      //this.countTime()
    }
  }
</script>

<style lang="less" scoped>
  .groupon_haoyou1{
    margin-bottom: 80px;
    .tishi{
      background: rgba(255,85,0,0.10);
      height:44px;
      line-height: 44px;
      font-size: 14px;
      color: #444444;
      img{
        width:28px;
        height:28px;
        border-radius: 14px;
        margin-left:22px;
      }
      span{
        margin-left:10px;
      }
    }
    .share_topic{
      /* width: 335px; */
      /* height: 200px; */
      background: #FFFFFF;
      -webkit-box-shadow: 0 4px 20px 0 #E8E8EB;
      box-shadow: 0 4px 20px 0 #E8E8EB;
      border-radius: 8px;
      /* margin: 20px auto 0; */
      margin: 20px;
      .topic{
        font-size: 20px;
        color: #FF5500;
        /*height:20px;*/
        line-height: 20px;
        text-align: center;
        /*top:100px;*/
        /*padding-top:59px;*/
        border-radius: 8px;
        img{
          border-top-left-radius: 8px;
          border-top-right-radius: 8px;
        }
      }
      .desc{
        font-size: 14px;
        color: #333333;
        line-height: 20px;
        /*text-align: center;*/
        padding: 15px;
      }
    }
    .count_down{
      /*margin: 20px;*/
      /*width: 375px;*/
      .remaining_time{
        font-size: 15px;
        color: #444444;
        text-align: center;
      }
      .time{
        height: 48px;
        width: 288px;
        margin: 15px auto 0;
        strong,em{
          width:30px;
          height:48px;
          line-height: 48px;
          background:#FF5500;
          border-radius: 3px;
          text-align: center;
          color:#FFFFFF;
          font-size:30px;
          font-weight: normal;
          margin-right:5px;
          display:block;
          float:left;
          font-style: normal;
        }
        cite{
          font-size: 13px;
          color: #333333;
          font-style: normal;
          height:48px;
          line-height: 48px;
          width:18px;
          display:block;
          float:left;
          margin-left:5px;
          margin-right:3px;

        }
      }
      .road{
        font-size: 14px;
        color: #FF5500;
        height:14px;
        line-height: 14px;
        text-align: center;
        margin:10px auto 20px;
      }
    }
    .invitation_friends{
      height:123px;
      background: #FFFFFF;
      box-shadow: 0 4px 20px 0 #E8E8EB;
      border-radius: 8px;
      margin:20px;
      p{
        font-size: 15px;
        color: #333333;
        text-align: center;
        padding-top:13px;
        em{
          color: #FF5500;
          font-style: normal;
        }
      }
      .myimages{
        width:240px;
        margin:0 auto;
        img{
          width:60px;
          height:60px;
          border-radius: 30px;
          margin-top:14px;
        }
        img:nth-child(2){
          margin-left:9.6%;
        }
        img:nth-child(3){
          margin-left:10.7%;
        }
        img:nth-child(4){
          margin-left:10.7%;
        }

      }

    }
    .groupon_pay{
      width: 100%;
      height: 50px;
      line-height: 50px;
      position: fixed;
      bottom: 0;
      border-top: 1px solid #C8C8C8;
      background: #FFFFFF;
      /* margin: auto; */
      /* left: 0; */
      /* right: 0; */
      .pay_box1{
        width:60%;
        float:left;
        p,span,em,strong{
          display:block;;
          float:left;
          font-style: normal;
          font-weight: normal;
        }
        p{
          margin-left:18px;
          font-size: 18px;
        }
        span{
          margin-left: 14px;
          color: #FF5500;
        }
        em{
          margin-left:10px;
          text-decoration: line-through;
          font-size: 12px;
          color: #9B9B9B;
        }
      }
      .pay_box2{
        width:40%;
        float:right;
        strong{
          width: 100%;
          height: 50px;
          background: #FF5500;
          color: #FFFFFF;
          font-size: 18px;
          text-align: center;
          display: block;
        }
      }


    }
    .share_weixin_friends{
      height:160px;
      margin:30px 20px;
      background: #FFFFFF;
      box-shadow: 0 4px 20px 0 #E8E8EB;
      border-radius: 8px;
      p{
        font-size: 15px;
        color: #333333;
        text-align: center;
        padding-top:13px;
        em{
          color: #FF5500;
          font-style: normal;
        }
      }
      .fenhaopengyou{
        width:196px;
        margin:0 auto;
        .wx_fr_box{
          width:60px;
          height:82px;
          float:left;
          margin-top:29px;
          img{
            width:60px;
            height:60px;
          }
          span{
            display: block;
            height:22px;
            width:60px;
            text-align: center;
            font-size: 12px;
            color: #444444;
            margin-top:5px;
          }
        }
        .wx_fr_box:nth-child(2){
          margin-left:66px;
        }
        .wx_fr_box:nth-child(3){
          margin-left:70px;
        }

      }

    }
    //
    .shareMask{
      position: fixed;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.7);
      top: 0;
      left: 0;
      z-index: 999;
      .shareImg{
        width: 64px;
        height: 64px;
        margin-top: 64px;
        position: absolute;
        right: 30px;
        img{
          display: inline-block;
          width: 100%;
        }
      }
      .shareTip{
        font-size: 15px;
        color: #ffffff;
        font-family: PingFangSC-Regular;
        margin-top: 138px;
        text-align: center;
      }
    }
    .view_order{
      height:100px;
      font-size: 18px;
      text-align: center;
      margin:0 20px 50px 20px;
      span{
        color: #FFFFFF;
        border-radius: 25px;
        background: #FF5500;
        height:50px;
        line-height: 50px;
        display: block;
      }
    }
  }

</style>



