import request from '@/utils/ajax'

//
export function share(params) {
  return request({
    url: '/beec/wx/getShareInfo',
    method: 'post',
    data: params
  });
}

//
export function authorize(params) {
  return request({
    url: '/beec/wx/authorize?'+params,
    method: 'get',
   // data: params
  });
}

//
export function reload(params) {
  return request({
    url: '/beec/wx/reload',
    method: 'post',
    data: params
  });
}







